.new-chat-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.new-chat-card {
    /* min-height: 300px; */
    min-width: 500px;
    /* max-height: 500px; */
    max-width: 600px;

    background-color: white;
    border-radius: 30px;
    box-shadow: 0px 0px 30px 10px rgba(218, 218, 218, 0.382);
}

h4 {
    color: #2397d1;
}

/* Just like with other projects, there does not appear to be a transition with the label but this is nevertheless close enough */

.auth-form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    padding: 2rem;
    transition: 0.8s ease;
    /* background: #fff; */
}

.auth-form-field {
    display: flex;
    flex-direction: column;
    position: relative;

    margin: 1rem 0rem;
}

.auth-form-field label {
    margin-bottom: 0.45rem;
    color: #2397d1;
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.7px;
    line-height: 1.3;
}

.auth-form-field input {
    padding: 0.55rem 0.4rem;
    border: 1px solid #dcdddd;
    border-radius: 4px;
    font-size: 14px;
    outline: none;
    transition: all 150ms ease-in-out 0s;
    background: white;
    display: inline-block;
}

.auth-form-field input::placeholder {
    color: #9b9b9b;
    width: 100%;
    font-weight: unset;
}

.auth-form-field input:hover {
    border-color: #2397d1;
}

.auth-form-field input:focus,
.auth-form-field input:active {
    box-shadow: 0px 0px 0px 1.5px #2397d1;
    border-color: #2397d1;
}

.auth-form-field input {
    color: rgb(36, 36, 36)
}

.auth-form-error-message {
    color: #2397d1;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 13px;
    text-align: center;

}

.auth-form-button {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
}

.auth-form-button button {
    width: 50%;
    border-radius: 25px;
    background: #2397d1;
    border: 3px solid #2397d1;
    color: white;
    font-weight: 800;
    padding: 0.7rem 1.2rem;
    outline: none;
    cursor: pointer;
    transition: 0.3s ease;
}

.auth-form-button button:hover {
    background: #1d7aa9; /* changed a little */
}

.auth-form-button button:disabled {
    background: #939393; /* changed a little */
    border-color: #939393;
    cursor: default;
}

.auth__form-container_image {
    flex: 3;
    display: flex;
    box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.05);
}

.auth__form-container_image img {
    width: 100%;
    height: 100%;
}

.auth__form-container_fields-content p {
    font-size: 1.5rem;
    font-family: Arial, Helvetica, sans-serif;
    color: #05245a;
    font-weight: 900;
}

.auth__form-container_fields-account {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-top: 0.2rem;
}

.auth__form-container_fields-account p {
    font-size: 14px;
    color: #000;
    font-weight: 500;
}

.auth__form-container_fields-account span {
    color: #EE230C;
    cursor: pointer;
    font-weight: 700;
}

@media screen and (max-width: 800px) {
    .auth__form-container {
        flex-direction: column-reverse;
    }

    .auth__form-container_fields {
        justify-content: flex-start;
    }

    .auth__form-container_image {
        height: 100px;
        flex: none;
        box-shadow: none;
    }

    .auth__form-container_image img {
        object-fit: cover;
    }
}

@media screen and (max-width: 375px) {
    .auth__form-container_fields {
        padding: 2rem 0.5rem;
    }

    .auth__form-container_fields-content_input input {
        width: 95%;
    }

    .auth__form-container_fields-content_input-password {
        right: 3%;
    }
}

/* Field validation */
/* !!! this likely does not work perfectly because the other styles also change other aspects of the border like the width */
.auth-form-field input.field-valid,
.auth-form-field input.field-valid:hover,
.auth-form-field input.field-valid:focus,
.auth-form-field input.field-valid:active {
    border-color: green !important;
}

.auth-form-field input.field-invalid,
.auth-form-field input.field-invalid:hover,
.auth-form-field input.field-invalid:focus,
.auth-form-field input.field-invalid:active {
    border-color: red !important;
}